import React from "react";

const shapes = { circle: "rounded-[50%]", round: "rounded-[10px]" } as const;
const variants = {
  fill: {
    deep_orange_500: "bg-deep_orange-500 text-white-A700",
    deep_orange_500_38: "bg-deep_orange-500_38 text-deep_orange-500",
    deep_orange_500_3a: "bg-deep_orange-500_3a text-deep_orange-500",
    black_900: "bg-black-900 text-white-A700",
    white_A700_77: "bg-white-A700_77 text-white-A700",
    blue_gray_100_35: "bg-blue_gray-100_35 text-gray-800",
    white_A700: "bg-white-A700 shadow-bs",
    deep_orange_500_26: "bg-deep_orange-500_26 text-white-A700",
    gray_500_1e: "bg-gray-500_1e text-black-900_54",
  },
  outline: {
    gray_900: "border border-gray-900 border-solid text-black-900",
    white_A700: "border border-solid border-white-A700 text-white-A700",
    black_900_3a: "border border-black-900_3a border-solid text-gray-900_47",
    black_900: "border border-black-900 border-solid text-black-900",
    blue_gray_100: "border border-blue_gray-100 border-solid text-gray-500",
    deep_orange_500:
      "border border-deep_orange-500 border-solid text-deep_orange-500",
  },
} as const;
const sizes = {
  xs: "p-2.5",
  sm: "p-[13px]",
  md: "pl-[13px] pr-2 py-3.5",
  lg: "p-4",
  xl: "px-3 py-[18px]",
  "2xl": "p-[21px] sm:px-5",
  "3xl": "p-6 sm:px-5",
} as const;

export type ButtonProps = Omit<
  React.DetailedHTMLProps<
    React.ButtonHTMLAttributes<HTMLButtonElement>,
    HTMLButtonElement
  >,
  "onClick"
> &
  Partial<{
    className: string;
    shape: keyof typeof shapes;
    variant: keyof typeof variants;
    size: keyof typeof sizes;
    color: string;
    leftIcon: React.ReactNode;
    rightIcon: React.ReactNode;
    onClick: () => void;
  }>;

const Button: React.FC<React.PropsWithChildren<ButtonProps>> = ({
  children,
  className = "",
  leftIcon,
  rightIcon,
  shape = "",
  size = "",
  variant = "",
  color = "",
  ...restProps
}) => {
  return (
    <button
      className={`${className} ${(shape && shapes[shape]) || ""} ${
        (size && sizes[size]) || ""
      } ${(variant && variants[variant]?.[color]) || ""}`}
      {...restProps}
    >
      {!!leftIcon && leftIcon}
      {children}
      {!!rightIcon && rightIcon}
    </button>
  );
};

export { Button };
