import React from "react";
import { ErrorMessage } from "../../components/ErrorMessage";

const shapes = { round: "rounded-[10px]" } as const;
const variants = {
  outline: {
    blue_gray_100: "border border-blue_gray-100 border-solid",
    white_A700: "border border-solid border-white-A700 text-white-A700",
  },
  fill: {
    deep_orange_500_47: "bg-deep_orange-500_47 text-deep_orange-500",
    white_A700: "bg-white-A700 text-gray-900_03",
    white_A700_01: "bg-white-A700_01 text-gray-800_01",
    gray_600_14: "bg-gray-600_14 text-black-900",
    gray_500_1e: "bg-gray-500_1e",
  },
} as const;
const sizes = {
  xs: "pb-[13px] pt-[15px] px-[13px]",
  "2xl": "pb-[17px] pt-[25px] px-[17px]",
  md: "p-[19px]",
  lg: "pb-5 pl-[19px] pr-5 pt-[22px]",
  xl: "pb-[27px] pt-[23px] sm:px-5 px-[23px]",
  "3xl": "pb-[27px] pl-[23px] pr-[26px] pt-[26px] sm:px-5",
} as const;

export type InputProps = Omit<
  React.DetailedHTMLProps<
    React.InputHTMLAttributes<HTMLInputElement>,
    HTMLInputElement
  >,
  "size" | "prefix" | "type" | "onChange"
> &
  Partial<{
    wrapClassName: string;
    className: string;
    name: string;
    placeholder: string;
    type: string;
    errors: string[];
    label: string;
    prefix: React.ReactNode;
    suffix: React.ReactNode;
    onChange: Function;
    shape: keyof typeof shapes;
    variant: keyof typeof variants;
    size: keyof typeof sizes;
    color: string;
  }>;

const Input = React.forwardRef<HTMLInputElement, InputProps>(
  (
    {
      wrapClassName = "",
      className = "",
      name = "",
      placeholder = "",
      type = "text",
      children,
      errors = [],
      label = "",
      prefix,
      suffix,
      onChange,
      shape = "",
      size = "",
      variant = "",
      color = "",
      ...restProps
    },
    ref,
  ) => {
    const handleChange: React.ChangeEventHandler<HTMLInputElement> = (e) => {
      if (onChange) onChange(e?.target?.value);
    };

    return (
      <>
        <div
          className={`${wrapClassName} 
              ${(shape && shapes[shape]) || ""} 
              ${(variant && variants[variant]?.[color]) || ""} 
              ${(size && sizes[size]) || ""}`}
        >
          {!!label && label}
          {!!prefix && prefix}
          <input
            ref={ref}
            className={`${className} bg-transparent border-0`}
            type={type}
            name={name}
            placeholder={placeholder}
            onChange={handleChange}
            {...restProps}
          />
          {!!suffix && suffix}
        </div>
        {!!errors && <ErrorMessage errors={errors} />}
      </>
    );
  },
);

export { Input };
